import { Component, macro, tween, Tween, v3, Vec2, Vec3, _decorator } from 'cc';
import { cocosUtil } from '../../../../utils/cocosUtil';
import { utilTools } from '../../../../utils/utilTools';
import { mapModel } from '../../../model/mapModel';
import { physicsGroup } from '../../../other/physicsGroup';
import { Bullet } from './Bullet';
import { Node } from 'cc';
import { find } from 'cc';
const { ccclass, property } = _decorator;

// -
@ccclass('bullet1111')
export class bullet1111 extends Component {

    bullet: Bullet;
    hasTweenPlay: boolean = false;

    // 
    outScreenNotRecycle: boolean = true;

    // 
    lv: number;
    targetDis: number = 260;

    // 
    radianSpeed: number;
    radian: number;

    shotTimeCount: number;

    timeCount: number; 
    atkCd: number; //

    direction: Vec3;
    targetNode: Node;

    fireNode: Node;

    fireAtk: number; // 

    init(bullet: Bullet) {
        this.bullet = bullet;

        this.fireNode = find("fire", this.node);

        this.node.angle = 0;
        this.radianSpeed = 0;

        this.timeCount = 0;
        cocosUtil.tweenScaleIn(this.node, 0.5);

        this.setUpDownTween(true);

        this.lv = this.bullet.extraObj.lv;
        this.fireAtk = this.bullet.extraObj.atk;

        this.scheduleOnce(() => {
            cocosUtil.tweenScaleOut(this.node, 0.5, () => {
                this.bullet.recycleSelf();
            });
        }, this.bullet.extraObj.time);

        this.schedule(()=>{
            this.shot();
        }, this.bullet.extraObj.atkCd,macro.REPEAT_FOREVER, 0);
    }

    setUpDownTween(isOpen: boolean) {
        if (isOpen) {
            if (!this.hasTweenPlay) {
                this.hasTweenPlay = true;
                tween(this.node).to(0.3, { scale: v3(0.9, 1.1, 1) }).to(0.5, { scale: v3(1.2, 0.89, 1) }).union().repeatForever().start();
            }
            return;
        }

        if (this.hasTweenPlay) {
            this.hasTweenPlay = false;
            Tween.stopAllByTarget(this.node);
        }
    }

    shot() {
        let extraObj = {
            atk: this.fireAtk,
        }
        mapModel.bulletSystem.addBullet(this.bullet.id+1, physicsGroup.PLAYER_BULLET, cocosUtil.convertToWorldSpace(this.fireNode), this.direction, extraObj);
    }

    update(dt:number){
        // 
        let targetNode = mapModel.mapSystem.getMinDisTargetNode(this.node.position);
        if (!targetNode) {
            return;
        }

        this.targetNode = targetNode;
        let pos1 = cocosUtil.convertToWorldSpace(this.node);
        let pos2 = cocosUtil.convertToWorldSpace(targetNode);
        // //   tween
        // let scale = this.node.getScale();
        // if (pos1.x <= pos2.x) {
        //     this.node.setScale(v3(Math.abs(scale.x), scale.y, scale.z));
        //     this.leftOrRight = 1;
        // } else {
        //     this.node.setScale(v3(-Math.abs(scale.x), scale.y, scale.z));
        //     this.leftOrRight = -1;
        // }

        // 
        pos2.subtract(pos1);
        this.direction = pos2;
        let angle = utilTools.radianToAngle(Math.atan2(pos2.y, pos2.x));
        this.node.angle = 0;
    }

    recycleSelfEnd() {
        // 
        if (cocosUtil.isValid(this.bullet.extraObj.invokeClass)) {
            this.bullet.extraObj.invokeClass.subNum();
        }
    }

}

