import { Node, tween, v3, _decorator } from 'cc';
import { BaseLayer } from '../base/BaseLayer';
import { ProgressBar } from 'cc';
import { constants } from '../../data/constants';
import { mapModel } from '../../model/mapModel';
const { ccclass, property } = _decorator;

@ccclass('TransLayer')
export class TransLayer extends BaseLayer {

    iconNode: Node;
    blackNode: Node;
    processBar: Node;
    process: number;
    percentNode: Node;

    onLoad() {
        super.onLoad();

        this.iconNode = this.getNodeByPath("icon");
        this.blackNode = this.getNodeByPath("black");
        this.processBar = this.getNodeByPath("ProgressBar");
        this.percentNode = this.getNodeByPath("ProgressBar/Label/percent");

        // this.processBar.active = false; //

        {   //
            if (this.layerCb) {
                this.layerCb((percent: number) => {
                    // TODO 
                    this.process = percent;
                }, () => {
                    this.openLayer(constants.layers.MapLayer, this.layerData, null, () => {
                        // 
                        this.closeLayer(constants.layers.TransLayer);
                        this.closeLayer(constants.layers.TowerFloorLayer);
                        this.closeLayer(constants.layers.HomeLayer);
                    });
                });
            }
        }
    }

    initUI() {
        this.blackNode.active = false;
        this.iconNode.scale = v3(5, 5, 1);
        tween(this.iconNode).to(0.5, { scale: v3(0.05, 0.05, 1) }, { easing: "quadOut" }).call(() => {
            this.blackNode.active = true;
        }).start();
    }

    // 
    update(dt: number): void {
        if (this.process > 0) {
            let process = this.process;
            process = Math.ceil(process * 10000) / 10000;
            if (this.process <= 1) {
                this.setProgressBar(this.processBar, process);
                this.setString(this.percentNode, Math.ceil(process * 100) + "%");
                // this.iconNode.scale = v3(5*(1-process)+0.05,5*(1-process)+0.05,1);
            }
        }
    }
}

