import { _decorator, Node, instantiate, Label, PageView, Sprite, Layout, v3, Prefab, Button } from 'cc';
import { myLog } from '../../../common/myLog';
import { sdkManager } from '../../../sdk/sdkManager';
import { constants } from '../../data/constants';
import { audioManager } from '../../manager/audioManager';
import { designManager } from '../../manager/designManager';
import { RedPointLogicMgr, RPointMask } from '../../manager/RedPointLogicMgr';
import { UserData } from '../../model/UserData';
import { PropItemPrefab } from '../item/PropItemPrefab';
import { BaseUILayer } from './BaseUILayer';
import { eventManager } from '../../manager/eventManager';
import { msgac } from '../../data/msgac';

const { ccclass, property } = _decorator;

// 
const MAX_RewardsNum = 3;

@ccclass('ChapterLayer')
export class ChapterLayer extends BaseUILayer {
    @property({ type: Node, tooltip: "" }) mapName: Node = null;
    @property({ type: Node, tooltip: "" }) mapItem: Node = null;
    @property({ type: Node, tooltip: "pageView" }) pageContent: Node = null;
    @property({ type: Node, tooltip: "" }) selectBtn: Node = null;

    @property({ type: Node, tooltip: "" }) rewardLayer: Node = null;

    @property({ type: PageView, tooltip: "" }) mapPage: PageView = null;

    @property({ type: Prefab, tooltip: "" }) rewardItem: Prefab = null;

    @property({ type: Label, tooltip: "1" }) tishiLabel: Label = null;
    @property({ type: Label, tooltip: "2" }) tishiLabel2: Label = null;

    @property({ type: Node, tooltip: "" }) btn1: Node = null;
    @property({ type: Node, tooltip: "" }) btn2: Node = null;


    _curPageId = 0
    _curOpenLevel = 1
    _curRewardList = []
    _curIndex = 0

    onLoad() {
        let mapData = designManager.getTable(constants.tableName.map)
        // let curMap = UserData.getInstance().getCurMap()
        // this._curOpenLevel = curMap
        // this.setSpriteFrame2(this.mapName, "homeLayer/map_name" + (mapData[curMap - 1].id), null, constants.bundles.wwqVec)
        // this.mapName.localize(`name${mapData[curMap - 1].id}`,constants.catName.mapSystem);
        for (let index = 0; index < MAX_RewardsNum*UserData.getInstance().openLevel; index++) {
            let item = instantiate(this.mapItem)
            this.pageContent.addChild(item)
            // item.getChildByName("timeLabel").getComponent(Label).string = (index * 5 + 5) + ""
            // item.getChildByName("chapterNum").getComponent(Label).string = "" + curMap + ""
            item.getChildByName("timeLabel").localize({cat:constants.layers.ChapterLayer, key:"timeLabel", vars:[index%MAX_RewardsNum * 5 + 5]});
            item.getChildByName("chapterNum").localize({cat:constants.layers.ChapterLayer, key:"chapterNum", vars:[Math.floor(index/MAX_RewardsNum)+1]});
        }
    }

    start() {
        this.node.getChildByPath("Label").localize("awardTxt", constants.layers.ChapterLayer);
        // let curMap = UserData.getInstance().getCurMap() - 1
        // // this.setPage(curMap)
        // let leveldata = UserData.getInstance().getLevelData(UserData.getInstance().getCurMap())
        for(let i = 0; i < MAX_RewardsNum*UserData.getInstance().openLevel;i++){
            let level = Math.floor(i/MAX_RewardsNum)+1;
            let leveldata = UserData.getInstance().getLevelData(level)
            // if (leveldata.Time >= ((i%MAX_RewardsNum+1) * 5 ) * 60) {
                if (leveldata.ReceiveState[i%MAX_RewardsNum] == 0) {
                    this.mapPage.scrollToPage(i)
                    this.setPage(i, level)
                    this._curOpenLevel = level;
                    return
                }
            // }
        }

        this.mapPage.scrollToPage(0)
        this.setPage(0,1)
    }

    getLevelByScrollIndex(index:number){
        return Math.floor(index/MAX_RewardsNum)+1;
    }

    /**
     * 
     * @param index 5-110-2-3
     * @param level 
     */
    setPage(index, level) {
        let mapData = designManager.getTable(constants.tableName.map)
        let curMap = level
        let children = this.pageContent.children
        for (let i = 0; i < children.length; i++) {
            let element = children[i];
            element.getChildByName("light").active = i == index
            if (i == index) {
                element.scale = v3(1, 1, 1)
            } else {
                element.scale = v3(0.8, 0.8, 1)
            }
        }

        // this.tishiLabel.string = "" + (index * 5 + 5) + ""
        // this.tishiLabel2.string = "" + (index * 5 + 5) + ""
        this.tishiLabel.node.localize({cat:constants.layers.ChapterLayer, key:"tips1", vars:[index * 5 + 5]});
        this.tishiLabel2.node.localize({cat:constants.layers.ChapterLayer, key:"tips2", vars:[index * 5 + 5]});

        this._curIndex = index
        this._curOpenLevel = level;
        this._curRewardList = mapData[curMap - 1]["reward" + (index + 1)]

        //  this._curRewardList.push([204,1000])
        this.updateBtnState()
        this.rewardLayer.removeAllChildren()
        for (let i = 0; i < this._curRewardList.length; i++) {
            let element = this._curRewardList[i];
            //  let dataArr = this.stringToArr(element)
            let item = instantiate(this.rewardItem)
            item.position = v3(0, 0, 0)
            this.rewardLayer.addChild(item)
            item.getComponent(PropItemPrefab).setView(element[0], element[1])
        }
    }

    updateBtnState() {
        // let leveldata = UserData.getInstance().getLevelData(UserData.getInstance().getCurMap())
        let leveldata = UserData.getInstance().getLevelData(this._curOpenLevel);
        this.btn1.getComponent(Button).interactable = false
        this.btn2.getComponent(Button).interactable = false

        this.btn2.getComponent(Sprite).color = constants.colors.yin.parseColor()
        this.btn1.getComponent(Sprite).color = constants.colors.yin.parseColor()

        this.btn2.getChildByName("Label").localize("getDouble", constants.catName.common);

        RedPointLogicMgr.ins().setRPointView(this.btn1,null,false)
        if (leveldata.Time >= ((this._curIndex+1) * 5) * 60) {
            if (leveldata.ReceiveState[this._curIndex] == 0) {
                this.btn1.getComponent(Button).interactable = true
                this.btn1.getComponent(Sprite).color = constants.colors.white.parseColor()
                // this.btn1.getChildByName("Label").getComponent(Label).string = " "
                this.btn1.getChildByName("Label").localize("get", constants.catName.common);
                RedPointLogicMgr.ins().setRPointView(this.btn1,null,true)
            } else {
                // this.btn1.getChildByName("Label").getComponent(Label).string = ""
                this.btn1.getChildByName("Label").localize("hasGet", constants.catName.common);
            }
            if (leveldata.ReceiveState[this._curIndex] < 2) {
                this.btn2.getComponent(Button).interactable = true
                this.btn2.getComponent(Sprite).color = constants.colors.white.parseColor()
            }
        } else {
            // this.btn1.getChildByName("Label").getComponent(Label).string = " "
            this.btn1.getChildByName("Label").localize("get", constants.catName.common);
        }
    }

    scrowPage(event) {
        myLog.i("event ", event)
        this.setPage(event._curPageIdx%MAX_RewardsNum, this.getLevelByScrollIndex(event.curPageIdx))
    }

    myScrollPage() {
        let nextIdx = this.mapPage.curPageIdx+1;
        let getNextIdx = ()=> {
            let levelData = UserData.getInstance().getLevelData(this.getLevelByScrollIndex(nextIdx));
            if (levelData.ReceiveState[nextIdx%MAX_RewardsNum] == 0 || nextIdx == this.mapPage.getPages().length-1) {
                return nextIdx;
            }else{
                nextIdx++;
                getNextIdx();
            }
        }
        this.mapPage.scrollToPage(getNextIdx(),1);
    }

    onGetReward() {
        audioManager.playEffect("btnClick")

        let curRewardArr = UserData.getInstance().tobePropItem(this._curRewardList)
        UserData.getInstance().getRewardProp(curRewardArr, 1)
        this.showGetReward(curRewardArr)
        let leveldata = UserData.getInstance().getLevelData(this._curOpenLevel)
        leveldata.ReceiveState[this._curIndex] = 1
        UserData.getInstance().setLevelData(leveldata)
        this.updateBtnState()
        this.scheduleOnce(this.myScrollPage,0.5);
        eventManager.send(msgac.refreshRedPoint);
     //   RedPointLogicMgr.ins().sendValueSettingMsg(RPointMask.RPM_PropWear, false)
    }

    onGetRewardDouble() {
        audioManager.playEffect("btnClick")

        sdkManager.openAd("" + (this._curIndex * 5 + 5) + "", (st: number) => {
            if (st == 1) {
                let curRewardArr = UserData.getInstance().tobePropItem(this._curRewardList)

                UserData.getInstance().getRewardProp(curRewardArr, 2)
                this.showGetReward(curRewardArr)

                let leveldata = UserData.getInstance().getLevelData(this._curOpenLevel)
                leveldata.ReceiveState[this._curIndex] = 2
                UserData.getInstance().setLevelData(leveldata)
                this.updateBtnState()
                this.scheduleOnce(this.myScrollPage,0.5);
                eventManager.send(msgac.refreshRedPoint);
            }
        });
    }


    onDisable() {
    }

    onDestroy() {
    }

    backHome() {
        audioManager.playEffect("btnClick")
        myLog.i("backHome")
        this.openLayer(constants.layers.HomeLayer);
        this.closeLayer()
    }

}

