import {find, _decorator, Node, Toggle, ToggleContainer, instantiate} from 'cc';
import { audioManager } from '../../manager/audioManager';

import { BaseUILayer } from './BaseUILayer';
import {UserData} from "db://assets/scripts/game/model/UserData";
import * as cc from "cc";
import {designManager} from "db://assets/scripts/game/manager/designManager";
import {localizeManager} from "db://assets/scripts/localization";
import {constants} from "db://assets/scripts/game/data/constants";
import {resManager} from "db://assets/scripts/game/manager/resManager";
import {eventManager} from "db://assets/scripts/game/manager/eventManager";
import {msgac} from "db://assets/scripts/game/data/msgac";

const { ccclass, property } = _decorator;

@ccclass('SettingLayer')
export class SettingLayer extends BaseUILayer {

    @property({ type: Node }) musicToggle: Node = null;
    @property({ type: Node }) effectToggle: Node = null;
    @property({ type: Node }) toggleContainer: Node = null;

    public textMusic : Node = null;
    private textSound : Node = null;
    private textTitle : Node = null;
    private textClose : Node = null;

    private langItem : Node = null; //Toggle

    onLoad() {
        super.onLoad();
        this.textMusic = find("ui/bg/Layout/Label_music",this.node);
        this.textSound = find("ui/bg/Layout/Label_sound",this.node);
        this.textTitle = find("ui/info",this.node);
        this.textClose = find("ui/btns/btnGoOn/Label_close",this.node);

        this.langItem = this.toggleContainer.children[0];
        let langObj = localizeManager.localization.lang;

        // //ToggleContainer
        //
        // let index = 0;
        // for (const key of Object.keys(langObj)) {
        //     index++;
        //     lz.LangIndex[key] = index;
        // }
        //
        // //
        // if (!lz.myFonts){
        //     lz.myFonts = {};
        //     for (const key in lz.LangIndex) {
        //         let fontarr = designManager.getTable(constants.tableName.font);
        //         let fontpath:string = null;
        //         for (const i in fontarr){
        //             let row = fontarr[i];
        //             if (row.key == key){
        //                 fontpath = row.font;
        //                 lz.myFonts[key].scale = row.scale;
        //                 break;
        //             }
        //         }
        //
        //         if (fontpath){
        //             resManager.loadAsset(constants.bundles.common,`font/${fontpath}`,cc.Font,null, (err,font)=>{
        //                 if (err) {
        //                     console.log("Failed to load font:", err);
        //                     lz.myFonts[key] = cc.Label.prototype.font;
        //                     return;
        //                 }
        //                 lz.myFonts[key].font = font;
        //             })
        //         }
        //     }
        // }

        //ToggleContainer
        for (const key in localizeManager.langIndex) {
            if (key == this.langItem.name){
                this.langItem.getChildByName("text").localizeFormat(key);
                continue;
            }
            let node:Node = instantiate(this.langItem);
            node.parent = this.toggleContainer;
            node.setPosition(this.langItem.position.x, this.langItem.position.y+localizeManager.langIndex[key]*-60);
            node.active = true;
            node.name = key;
            let texnode = node.getChildByName("text");
            texnode.getComponent(cc.Label).string = langObj[key];
            texnode.localizeFormat(key);
        }

        this.textMusic.localize("music",constants.layers.SettingLayer)
        this.textSound.localize("sound",constants.layers.SettingLayer)
        this.textTitle.localize("title",constants.layers.SettingLayer)
        this.textClose.localize("close","common")
    }

   

    onDisable() {
        super.onDisable();
    }

    onDestroy() {
        super.onDestroy();
    }

    start() {
        let isMusic = audioManager.getMusiceVolume()
        let isEffect = audioManager.getEffectVolume()
        let language = UserData.getInstance().$localization

        this.musicToggle.getComponent(Toggle).isChecked = (isMusic > 0);
        this.effectToggle.getComponent(Toggle).isChecked = (isEffect > 0);
        let lToggle = this.toggleContainer.getComponent(ToggleContainer).toggleItems[localizeManager.langIndex[language]-1];
        lToggle.isChecked = true;
    }

    onMusicCheck() {
        console.log("onMusicCheck", this.musicToggle.getComponent(Toggle).isChecked)
        // let isMusic = audioManager.getMusiceVolume()
        // if (isMusic <= 0) {
        //     isMusic = 1
        // } else {
        //     isMusic = 0
        // }
        audioManager.setMusicVolume(this.musicToggle.getComponent(Toggle).isChecked ? 1 : 0)
        // this.musicToggle.getComponent(Toggle).isChecked = isMusic > 0
    }

    onSoundCheck() {
        console.log("onEffectCheck", this.effectToggle.getComponent(Toggle).isChecked)
        // let isEffect = audioManager.getEffectVolume()
        // if (isEffect <= 0) {
        //     isEffect = 1
        // }else {
        //     isEffect = 0
        // }
        audioManager.setEffectVolume(this.effectToggle.getComponent(Toggle).isChecked ? 1 : 0)
    }

    onSingleToggleCheck(toggle: Toggle, index: number) {
        let toggleItems = this.toggleContainer.getComponent(ToggleContainer).toggleItems;
        index = toggleItems.indexOf(toggle);
        localizeManager.set_language(index)
        eventManager.send(msgac.refreshLocalization)

        this.textMusic.localize("music",constants.layers.SettingLayer)
        this.textSound.localize("sound",constants.layers.SettingLayer)
        this.textTitle.localize("title",constants.layers.SettingLayer)
        this.textClose.localize("close","common")

        console.log(`toggleContainer:${index},${toggle.isChecked}`);
        // UserData.getInstance().saveData();
    }

    closeLayer() {
        super.closeLayer()
    }



}

