class MyLog {
    private static _instance: MyLog;
    private constructor() { }
    public static get instance(): MyLog {
        if (!this._instance) {
            this._instance = new MyLog();
        }
        return this._instance;
    }

    isOpen: boolean = true;

    public i(...params: any[]) {
        if (!this.isOpen) {
            return;
        }
        // params.unshift(new Date().toLocaleTimeString());
        // console.info.apply(console, params);
        myLog.writeLog("info", ...params);
    }

    public d(...params: any[]) {
    
        
        // params.unshift(new Date().toLocaleTimeString());
        // console.debug.apply(console, params);

        myLog.writeLog("debug", ...params);
    }

    public w(...params: any[]) {
        if (!this.isOpen) {
            return;
        }
        // params.unshift(new Date().toLocaleTimeString());
        // console.warn.apply(console, params);

        myLog.writeLog("warn", ...params);
    }

    public e(...params: any[]) {
        myLog.writeLog("error", ...params);
    }

    public writeLog(level:string, ...params: any[]) {
        
        // const err = new Error();
        // const stackLines = err.stack?.split('\n');

        // let fileLineInfo = '';
        // if (stackLines && stackLines.length>2) {
        //     const callerLine = stackLines[2];
        //     const match = callerLine.match(/\((.*):(\d+):(\d+)\)/);
        //     if (match) {
        //         fileLineInfo = `${match[1]}:${match[2]}`;
        //     }
        // }

        // params.unshift(fileLineInfo);
        params.unshift(new Date().toLocaleTimeString());

        switch (level) {
            case "error":
                console.error.apply(console, params);
                break;
            case "warn":
                console.warn.apply(console, params);
                break;
            case "info":
                console.info.apply(console, params);
                break;
            case "debug":
                console.debug.apply(console, params);
                break;
            default:
                console.log(...params);
                break;
        }
    }
}


export const myLog = MyLog.instance;