import { Component, _decorator } from 'cc';
import { cocosUtil } from '../../../../utils/cocosUtil';
import { audioManager } from '../../../manager/audioManager';
import { mapModel } from '../../../model/mapModel';
import { physicsGroup } from '../../../other/physicsGroup';
import { Skill } from './Skill';
import { utilTools } from '../../../../utils/utilTools';
import { Bullet } from '../bullet/Bullet';
import { Vec3 } from 'cc';
const { ccclass, property } = _decorator;

// 
@ccclass('Skill11')
export class Skill11 extends Component {

    skill: Skill;

    timeCount: number = 0;

    num: number = 0;

    towerBulletArr: any[];

    extraObj:{};

    init(skill: Skill) {
        this.skill = skill;
    }

    subNum() {
        this.num--;
    }

    creatTower(extraObj?: any){
        if (!extraObj) {
            return;
        }
        // =(+skill2)*skill2
        let atk = extraObj.num+extraObj.addCoin;
        if (extraObj.addCoin>0) {
            atk = atk*extraObj.addCoin;
        }
        let exObj = {
            endCb: (bulletUtil: any) => {
                if (!cocosUtil.isValid(this.node)) {
                    bulletUtil.bullet.recycleSelf();
                    return;
                }
            },
            lv: this.skill.row.lv,
            atk: atk* (1 + mapModel.skillSystem.getSkill2AtkAddPercent()),
            time: this.skill.getDuration(),
            atkCd: this.skill.getAtkCd(),
            invokeClass: this,
        }
        mapModel.bulletSystem.addBullet(this.skill.row.bulletId, physicsGroup.PLAYER_BULLET2, extraObj.pos, null, exObj);
    }

    updateLogic(dt: number){
        
    }

}

